#infomation {
  .enqueteBox{
    &__ttl{
      @include font-line(18);
      color: #000000;
      border-bottom: 2px solid #000000;
      text-align: left;
      padding: 8px 10px;
      margin-top: 0;
      margin-bottom: 10px;
    }
  }
  #entry-comment {
    padding-left: 10px;
    margin: 10px 0 10px;
    @include font-line(14, 18);
  }
  .entry{
    padding: 10px 0;
    line-height: 1.5em;
    &__ttl{
      font-size:1.17em;
      background-color: #fffaf3;
      padding: 5px 10px;
      font-weight: normal;
      clear: both;
      margin: 1em 0;
      &:after {
        display: block;
        content: "";
        clear: both;
      }
      p {
        margin: 0;
        padding: 0;
      }
      .hissu {
        background: #F00;
        float: left;
        font-size: 70%;
        font-weight: normal;
        color: #FFF;
        padding: 2px 5px;
        margin-right: 5px;
      }
      .nini {
        background: #CCC;
        float: left;
        font-size: 70%;
        font-weight: normal;
        color: #FFF;
        padding: 2px 5px;
        margin-right: 5px;
      }
    }
    .tel {
      line-height: 2em;
      }
  }
  #pref {
    width: 206px;
  }
  .btnArea{
    &__btn{
      @include font-line(18);
      width: 100%;
    }
  }
}

.input-maxsize {
  width: 95%;
}

#infomation .entry .small-text {
  @include font-line(13);
}
#infomation .entry .small-text2 {
  font-size: 70%;
}

#kikkake label {
  width: auto;
  /* 	float:left; */
}

/*問い合わせ種別*/
#infomation {
  #toi-syubetu label {
    float: left;
    margin-bottom: 10px;
    margin-right: 5px;
  }
  .enter-TextBtn {
    text-align: center;
  }
  .entry {
    .questionnaire {
      background-color: #C00;
      font-weight: 400;
      color: #FFF;
      text-align: center;
    }
    .kojinjyoho {
      font-size: 12px;
      overflow: auto;
      height: 150px;
      padding: 10px 15px;
      margin: 20px 0;
      border-top: #231815 medium solid;
    }
    #doui-toi {
      p {
        font-size: 12px;
        margin-bottom: 10px;
        line-height: 130%;
      }
      margin-top: 10px;
      text-align: center;
      padding: 10px;
      margin-right: 10px;
      margin-bottom: 10px;
    }
  }
  ul {
    padding: 0;
  }
  li {
    list-style: none;
  }
  #sousin {
    text-align: center;
    margin-bottom: 20px;
  }
}

#new-member {
  .new-mConfirm {
    padding-left: 10px;
    h2 {
      background-color: #008002;
      color: #FFF;
      width: 100%;
      padding: 5px 0 5px 0;
      margin: 5px 0 0 0;
      text-align: center;
    }
    h3 {
      background-color: #79C1E8;
      text-align: center;
      color: #FFF;
    }
    dl {
      line-height: 120%;
      dt {
        background-color: #FAF5ED;
        padding-top: 5px;
        padding-bottom: 5px;
        margin-left: -10px;
        padding-left: 10px;
        margin-bottom: 5px;
        &:first-child {
          margin-top: -10px;
        }
      }
      dd {
        padding-top: 2px;
        padding-bottom: 5px;
        min-height: 15px;
        line-height: 140%;
      }
    }
    .hensyu-img {
      text-align: right;
      margin-right: 10px;
    }
    .small-text {
      font-size: 70%;
    }
  }
  .enter-TextBtn {
    font-size: 80%;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 5px;
    padding: 0 10px;
  }
  .enter-TextBtn2 {
    font-size: 80%;
    text-align: left;
    margin-top: 20px;
    margin-bottom: 5px;
    padding: 0 10px;
  }
}

/*小さいテキスト指定*/
#reserve {
  h2, .merit {
    margin-bottom: 20px;
  }
  .heijitsu {
    margin-bottom: 20px;
    .tit, li {
      margin-bottom: 10px;
    }
  }
  .att {
    .tit {
      color: #e60012;
      margin-bottom: 10px;
    }
    ul {
      list-style: disc;
      margin-left: 20px;
      li {
        margin-bottom: 10px;
        p {
          text-align: center;
        }
      }
    }
  }
  .raitenbi {
    select {
      width: 30%;
      margin-bottom: 10px;
    }
    p {
      line-height: 1.6em;
    }
    h4 {
      padding: 5px 0 5px;
      border-bottom: solid 1px #FAF5ED;
      margin-bottom: 5px;
      p {
        margin: 0;
        padding: 0;
      }
      .hissu {
        background: #F00;
        float: left;
        font-size: 70%;
        font-weight: normal;
        color: #FFF;
        padding-right: 5px;
        padding-left: 5px;
        margin-top: 2px;
        margin-right: 5px;
      }
      .nini {
        background: #CCC;
        float: left;
        font-size: 70%;
        font-weight: normal;
        color: #FFF;
        padding-right: 5px;
        padding-left: 5px;
        margin-right: 5px;
        margin-top: 2px;
      }
    }
    .kibou_in {
      margin-bottom: 10px;
      .disp_calender {
        display: inline-block;
        background-color: $color-primary;
        color: #fff;
        padding: 5px;
        width: 50px;
        height: 30px;
        text-align: center;
        border-radius: 5px;
        margin-right: 10px;
        vertical-align: middle;
      }
    }
    .infotime {
      @include font-line(14, 18);
    }
  }
  .mokuteki label {
    display: inline-block;
  }
  .textarea textarea {
    width: 90%;
  }
}

.entry-comment {
  line-height: 1.5em;
}

#reserve ol {
  padding: 0;
  li {
    list-style: none;
		a.disp_calender{
		  display: inline-block;
			background: $color-primary;
			color: #fff;
			padding: 3px 10px;
			border-radius: 4px;
			margin-left: 3px;
      border: none;
		}
  }
}

.moreinfo-title {
  line-height: 1.2em;
}

.pageSell{
  .att{
    &__txt{
      padding-bottom:10px;
      @include font-line(14, 18);
    }
  }
  &__ttl{
    color: #000000;
    border-bottom: 2px solid #000000;
    text-align: left;
    padding: 8px 10px;
    margin-top: 0;
    margin-bottom: 10px;
  }
}
